import React, { useEffect, useState } from '@wordpress/element';
import { PrettyEventData } from './types';
import Model from '../add-to-schedule/model';

export default function SinglePost({ data }: { data: PrettyEventData }) {
	const {
		locations,
		eventId,
		link,
		title,
		event_info,
		altText,
		srcSet,
		size,
		sizes,
		type,
	} = data;
	const [responseMessage, setResponseMessage] = useState('');
	useEffect(() => {
		const timeoutId = setTimeout(() => {
			setResponseMessage('');
		}, 7000);
		return () => clearTimeout(timeoutId);
	}, [responseMessage]);
	function addToSchedule(ev: Event) {
		Model.addToSchedule(ev)
			.then((response: string) => {
				let message = '';
				if ('success' === response) {
					message = `Added to your schedule!`;
				}
				if ('info' === response) {
					message = `This is already in your schedule.`;
				}
				setResponseMessage(`<div class="alert alert-${response}" role="alert">
					${message}
				</div>`);
			})
			.catch((err: any) => {
				console.error(err);
			});
	}
	return (
		<article className="cno-event">
			<figure className="cno-event__image">
				<img
					width={size.width}
					height={size.height}
					src=""
					className="attachment-large size-large wp-post-image"
					alt={altText}
					decoding="async"
					srcSet={srcSet}
					sizes={sizes}
				/>
			</figure>
			<h2>{title}</h2>
			<aside className="event-meta">
				<div className="event-meta__day">
					<strong>When: </strong>
					{event_info.info.day}, September 1
				</div>
				{locations.length > 0 && (
					<div className="event-meta__location">
						<strong>Where:</strong>{' '}
						<a href={locations[0].link} rel="tag">
							{locations[0].name}
						</a>
					</div>
				)}
				<div className="event-meta__start-time">
					<strong>Start Time:</strong>
					{' ' + event_info.info.startTime}
				</div>
				{event_info.info.endTime && (
					<div className="event-meta__end-time">
						<strong>End Time:</strong> {event_info.info.endTime}
					</div>
				)}
				<div className="event-meta__type">
					<strong>Event Type:</strong>{' '}
					<a href="${type[0].link}" rel="tag">
						{type[0].name}
					</a>
				</div>
			</aside>
			<div className="about">{event_info.description}</div>
			<div className="cno-event__buttons">
				<button
					className="btn__fill--primary"
					data-add-to-schedule="true"
					data-id={eventId}
					onClick={addToSchedule}>
					Add to Schedule
				</button>
				<a href={link} className="btn__outline--primary">
					Learn More
				</a>
				<div
					className="cno-event-schedule-confirmation"
					dangerouslySetInnerHTML={{ __html: responseMessage }}></div>
			</div>
		</article>
	);
}
